<template>
    <div>
        <div class="container">
            <el-form ref="filters" :inline="true" :model="filters" class="toolbar">
                <el-form-item label="车牌号" :inline="true">
                    <el-input v-model.trim="filters.plate_number" placeholder="请输入车牌号"></el-input>
                </el-form-item>
                <el-form-item label="车主" :inline="true">
                    <el-input v-model.trim="filters.band_name" placeholder="请输入车主"></el-input>
                </el-form-item>
                <!-- <el-form-item label="车辆类型" :inline="true">
                    <el-select v-model="filters.car_class" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-checkbox v-model="filters.is_call" true-label="1" false-label="2" class="pt8">挂靠公司</el-checkbox>
                <el-form-item label="挂靠公司">
                    <el-select v-model="filters.bus_mech_name" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="车辆状态">
                    <el-select v-model="filters.car_status" placeholder="请选择">
                        <el-option
                            v-for="item in options"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item> -->
                <el-button @click="handleSearch">查询</el-button>
                <el-button type="primary" icon="plus" @click="$router.push({ path: '/carFile/add'})">新建</el-button>
    
            </el-form>
            <el-table :data="tableData" border v-loading="loading">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="plate_number" label="车牌号" width="120">
                </el-table-column>
                <el-table-column prop="vin_number" label="vin号" width="120">
                </el-table-column>
                <el-table-column prop="bus_mech_name" label="挂靠公司" width="120">
                </el-table-column>
                <el-table-column prop="credit_type" label="信贷类型">
                </el-table-column>
                <el-table-column prop="band_name" label="车主">
                </el-table-column>
                <el-table-column prop="car_brand" label="车辆品牌">
                </el-table-column>
                <el-table-column prop="foreign_model" label="公告型号">
                </el-table-column>
                <el-table-column prop="car_status" label="车辆状态">
                </el-table-column>
                <el-table-column prop="create_user" label="创建人">
                </el-table-column>
                <el-table-column prop="create_time" label="创建日期">
                </el-table-column>
                <el-table-column label="操作" width="180" align="center" fixed="right">
                    <template slot-scope="{row, $index}">
                        <el-button type="text" icon="el-icon-view" @click="$router.push({ path: '/carFile/view', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">查看</el-button>
                        <el-button type="text" icon="el-icon-edit" @click="$router.push({ path: '/carFile/edit', query: {id:row.id, operation_uniqid: row.operation_uniqid}})">修改</el-button>
                        <el-button type="text" icon="el-icon-delete" class="red" @click="handleDelete(row, $index)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination background @current-change="handleCurrentChange" :page-size="limit" layout="prev, pager, next" :total="total">
                </el-pagination>
            </div>
        </div>
    
    </div>
</template>

<script>
    import * as API from '@/api/assetsFile'
    export default {
        data() {
            return {
                tableData: [],
                page: 1,
                limit: 10,
                total: 1,
                loading: false,
                filters: {
                    plate_number:'',
                    band_name:'',
                    car_class:'',
                    bus_mech_name:'',
                    is_call:'',
                    car_status:''
                },
                options: [{
                    value: '1',
                    label: '黄金糕'
                }, {
                    value: '2',
                    label: '双皮奶'
                }, {
                    value: '3',
                    label: '蚵仔煎'
                }, {
                    value: '4',
                    label: '龙须面'
                }, {
                    value: '5',
                    label: '北京烤鸭'
                }],
                idx: -1
            }
        },
        created() {
            this.getData();
        },
        computed: {
            
        },
        methods: {
            // 分页导航
            handleCurrentChange(val) {
                this.page = val;
                this.getData();
            },
            getData() {
                let params = {
                    page: this.page,
                    ...this.filters
                }
                this.loading = true
                API.apiCarFileList(params).then(res => {
                    this.loading = false
                    this.tableData = res.data;
                    this.total = res.total;
                })
               
            },
            handleSearch() {
                this.getData();
            },
            handleDelete(row, index) {
                let params = {
                    id: row.id,
                    operation_uniqid: row.operation_uniqid,
                    status: 2
                }
                API.apiCarFileDel(params).then(res => {
                    this.tableData.splice(index, 1);
                })
            }
        }
    }

</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .del-dialog-cnt{
        font-size: 16px;
        text-align: center
    }
    .table{
        width: 100%;
        font-size: 14px;
    }
    .red{
        color: #ff0000;
    }
</style>
